// mobile
.logo {
  position: absolute;
  top: 0px;
}

.logo-img {
  width: 107px;
}

.header-txt {
  padding-top: 78px;
  margin-bottom: 25px;
  //   position: absolute;
  margin-left: auto;
  margin-right: auto;

  width: 212px;

  font-weight: $font-weight-black;
  font-size: $font-size-18;
  line-height: 1.22;
  text-align: center;

  color: $title-text-color;
}

.background {
  // position: absolute;
  // top: 0px;
  background: url(../images/pic.jpg);
  width: 100%;
  height: 364px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
// tablet
@media screen and (min-width: 768px) {
  .header-txt {
    padding-top: 40px;
    width: 353px;
    font-size: $font-size-30;
  }
  .logo-img {
    width: 179px;
  }
  .background {
    background: url(../images/pic-tablet.jpg);
    width: 100%;
    height: 364px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

// desctop
@media screen and (min-width: 1280px) {
  .header-txt {
    width: 589px;
    font-size: $font-size-50;
    white-space: inherit;
  }

  .logo-img {
    width: 201px;
  }
  .background {
    margin: 0 auto;
    background: url(../images/pic-desctop.jpg);
    max-width: 1280px;
    height: 364px;
    //  background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.country-block {
  position: absolute;
  top: 187px;
  padding-top: 45px;
  width: 270px;
  height: 210px;
  background-color: $search-bg-color;
  backdrop-filter: blur(20px);
  border-radius: 20px;
  overflow-y: scroll;
}
.country-block:before {
  display: block;
  width: 260px;
  content: '';
  position: absolute;
  border-bottom: 1px solid #cccccc;
  top: 40px;
  left: 5px;
}
.drop-counties {
  margin-top: 6px;
  // border-top: 1px solid #cccccc;
  // margin-left: 30px;
  // margin-right: 30px;
}
.drop-country {
  padding-left: 30px;
  font-size: 16px;
  line-height: 1.25;
  color: #cccccc;
}
.drop-country:hover {
  background-color: $title-text-color;
}
.drop-country:not(:last-child) {
  margin-bottom: 2px;
}
::-webkit-scrollbar {
  width: 3px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  margin-top: 30px;
  margin-bottom: 15px;
}

::-webkit-scrollbar-thumb {
  background: $accent-color;
  border-radius: 20px;
  margin-top: 40px;
  margin-bottom: 15px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(54, 56, 58);
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;

  &::-ms-expand {
    display: none;
  }
}
.select-icon {
  position: absolute;
  top: 14px;
  right: 30px;
}

// country
.choose-input {
  z-index: 99;
  display: block;
  width: 100%;
  padding: 10px 30px;
  color: $accent-color;
  font-family: inherit;
  font-size: 16px;
  line-height: 1.25;
  border-radius: 20px;
  border-style: none;
  outline: none;
  cursor: pointer;
  background: $search-bg-color;
  backdrop-filter: blur(20px);

  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1) 0.2s;
  position: relative;
  margin-top: 25px;

  &:hover,
  &:focus {
    outline: none;
    box-shadow: 0 0 1px 1px $title-text-color;
  }

  @media screen and (min-width: 768px) {
    width: 270px;
    margin-top: 0;
    margin-left: 30px;
  }
  @media screen and (min-width: 1280px) {
    margin-left: 30px;
  }
}

.choose-country {
  display: block;
  text-align: start;
  position: relative;
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1) 0.2s;

  &::after {
    content: '';
    position: absolute;
    top: 20px;
    right: 30px;
    transform: translateY(-50%);
    display: block;
    width: 12px;
    height: 12px;
    top: 12px;
    right: 0;
  }
  &::before {
    content: '';
    position: absolute;
    top: 20px;
    right: 30px;
    transform: translateY(-50%);
    display: block;
    width: 12px;
    height: 12px;
    top: auto;
    bottom: -10px;
    right: -5px;
    width: 225px;
    height: 1px;
    border-bottom: 1px solid #cccccc;
    transform: translateY(-50%) scalex(0);
    transition: transform 0.3s cubic-bezier(0.19, 1, 0.22, 1) 0.2s;
  }

  &.active {
    outline: none;
    margin-bottom: 20px;

    &::after {
      background: url('../images/icons/sprite.svg#icon-arrow-down') no-repeat;
    }
    &::before {
      transform: translateY(-50%) scalex(1);
    }
  }
}

.country-list {
  width: 250px;
  margin-left: -30px;
  //padding-top: 15px;
  height: 210px;
  overflow-y: scroll;
  color: #cccccc;
  transform: scaleY(1);
  transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1) 0.2s;

  @media screen and (min-width: 768px) {
    width: 267px;
  }

  &::-webkit-scrollbar-thumb {
    -webkit-border-radius: 20px;
    border-radius: 5px;
    background-color: $accent-color;
  }

  &::-webkit-scrollbar {
    width: 3px;
  }

  .choose-input :not(.active) + & {
    padding-top: 0;
    height: 0;
    transform: scaleY(0);
  }
}

.country-button {
  height: 25px;
  width: 100%;
  align-items: center;
  color: $accent-color;
  text-align: start;
  padding-left: 30px;

  &:hover,
  &:focus,
  &.active,
  &.current {
    outline: none;
    background: $title-text-color;
    color: $primary-text-color;
    box-shadow: none;
  }
}
